// ÁîªÂ∏ÉÂ±ûÊÄß
const canvas = document.getElementById("map");
const ctx = canvas.getContext("2d");
const cellSize = 50;
const counter = document.getElementById("counter");
const ratio = window.devicePixelRatio;
let height = 9, width = 9;

// Êâ´Èõ∑Âú∞ÂõæÔºå0Ë°®Á§∫Êó†Èõ∑Ôºå1Ë°®Á§∫ÊúâÈõ∑
let minesMap;
// Âú∞ÂõæÁä∂ÊÄÅÔºå0Ë°®Á§∫Êè≠ÂºÄÔºå1Ë°®Á§∫Êú™Êè≠ÂºÄÔºå2Ë°®Á§∫ÊóóÂ∏ú
let mapStatus;
// Âú∞Èõ∑Êï∞ÁõÆ
let minesCount = 10;
// È¶ñÂºÄÊó†Èõ∑
let first = true;
// ÊóóÂ∏úËÆ°Êï∞
let flagCount;

// ÁªòÂà∂Êâ´Èõ∑Ê∏∏ÊàèÁΩëÊ†º
function drawGrid() {
    for (let row = 0; row < height; row++) {
        for (let col = 0; col < width; col++) {
            // ‰ΩøÁî®‰∫§ÊõøÈ¢úËâ≤ÔºöÂü∫‰∫éË°åÂàóÁ¥¢Âºï‰πãÂíåÂà§Êñ≠È¢úËâ≤
            if ((row + col) % 2 === 0) {
                ctx.fillStyle = "white"; // ‰∫ÆËâ≤
            } else {
                ctx.fillStyle = "lightgray"; // ÊöóËâ≤
            }
            // ÁªòÂà∂ÂçïÂÖÉÊ†º
            ctx.fillRect(col * cellSize, row * cellSize, cellSize, cellSize);
        }
    }
}

function generateMines(x, y) {
    let index = xy2XY(x, y), X = index[0], Y = index[1];
    let placedMines = 0;
    while (placedMines < minesCount) {
        // ÁîüÊàêÈöèÊú∫ÂùêÊ†á (1~height Âíå 1~width ËåÉÂõ¥ÂÜÖ)
        let row = Math.floor(Math.random() * height) + 1;
        let col = Math.floor(Math.random() * width) + 1;
        // Á°Æ‰øùËØ•‰ΩçÁΩÆÊ≤°ÊúâÂú∞Èõ∑
        if (row === Y && col === X) continue;
        if (minesMap[row][col] !== 1) {
            minesMap[row][col] = 1; // ÊîæÁΩÆÂú∞Èõ∑
            placedMines++;
        }
    }
}

function init() {
    // Ëß£ÂÜ≥ÊñáÂ≠óÊ®°Á≥äÈóÆÈ¢ò
    canvas.style.width = width * cellSize + "px";   // ÊòæÁ§∫ÂÆΩÂ∫¶
    canvas.style.height = height * cellSize + "px"; // ÊòæÁ§∫È´òÂ∫¶
    canvas.width = width * ratio * cellSize;    // ÂÆûÈôÖÂàÜËæ®ÁéáÂÆΩÂ∫¶
    canvas.height = height * ratio * cellSize;  // ÂÆûÈôÖÂàÜËæ®ÁéáÈ´òÂ∫¶
    ctx.scale(ratio, ratio);

    minesMap = Array.from({length: height + 2}, () => Array(width + 2).fill(0));
    mapStatus = Array.from({length: height + 2}, () => Array(width + 2).fill(1));
    first = true;
    flagCount = 0;

    counter.textContent = `Âç† ${minesCount - flagCount}`;
    drawGrid();
}

// Êç¢ÁÆóÂùêÊ†áxÔºåyÂà∞‰∏ãÊ†áXÔºåY
function xy2XY(x, y) {
    return [Math.floor(x / cellSize) + 1, Math.floor(y / cellSize) + 1];
}

// Êç¢ÁÆó‰∏ãÊ†áXÔºåYÂà∞ÂùêÊ†áxÔºåy
function XY2xy(X, Y) {
    return [(X - 1) * cellSize, (Y - 1) * cellSize];
}

// ËÆ°ÁÆóÈõ∑Êï∞
function sumMines(X, Y) {
    if (minesMap[Y][X] !== 0) {
        return -1;
    }

    let sum = 0;
    for (let i = -1; i < 2; i++) {
        for (let j = -1; j < 2; j++) {
            if (i === 0 && j === 0) continue;
            sum += minesMap[Y + i][X + j];
        }
    }
    return sum;
}

// ÁªòÂà∂Êï∞Â≠ó
function drawNum(cx, cy, num) {
    ctx.font = `${cellSize * 0.8}px Arial`;
    ctx.fillStyle = "white";
    ctx.fillText(num.toString(), cx + cellSize * 0.27, cy + cellSize * 0.78);
}

// Êè≠ÂºÄÂçï‰∏™Ê†ºÂ≠ê
function uncoverSingleGrid(X, Y) {
    if (X < 1 || Y < 1 || X > width || Y > height) return;

    let coord = XY2xy(X, Y), cx = coord[0], cy = coord[1];
    if (mapStatus[Y][X] === 1) {
        let num = sumMines(X, Y);
        if (num === -1) {
            alert("GAME OVER!");
            init();
        } else {
            ctx.clearRect(cx, cy, cellSize + 1, cellSize + 1);
            if (num !== 0) drawNum(cx, cy, num);
            mapStatus[Y][X] = 0;
            return num;
        }
    }
}

// Ê≠£Â∏∏Êè≠ÂºÄÊ†ºÂ≠ê
function uncoverGrid(x, y) {
    let index = xy2XY(x, y), X = index[0], Y = index[1];
    if (X < 1 || Y < 1 || X > width || Y > height) return;

    let coord = XY2xy(X, Y), cx = coord[0], cy = coord[1];
    let num = uncoverSingleGrid(X, Y);
    if (num === 0) {
        for (let i = -1; i < 2; i++) {
            for (let j = -1; j < 2; j++) {
                uncoverGrid(cx + i * cellSize, cy + j * cellSize);
            }
        }
    }
}

// Êè≠ÂºÄÂõõÂë®ÊâÄÊúâÊ†ºÂ≠ê
function uncoverAllGrid(x, y) {
    let index = xy2XY(x, y), X = index[0], Y = index[1];
    if (X < 1 || Y < 1 || X > width || Y > height || mapStatus[Y][X] !== 0) return;

    let coord = XY2xy(X, Y), cx = coord[0], cy = coord[1];
    let flags = 0;
    for (let i = -1; i < 2; i++) {
        for (let j = -1; j < 2; j++) {
            if (mapStatus[Y + i][X + j] === 2) flags++;
        }
    }
    if (flags >= sumMines(X, Y)) {
        for (let i = -1; i < 2; i++) {
            for (let j = -1; j < 2; j++) {
                uncoverGrid(cx + i * cellSize, cy + j * cellSize);
            }
        }
    }
}

// ÊóóÂ∏úËÆæÁΩÆ
function setFlag(x, y) {
    if (flagCount >= minesCount) return;

    let index = xy2XY(x, y), X = index[0], Y = index[1];
    let coord = XY2xy(X, Y), cx = coord[0], cy = coord[1];
    if (mapStatus[Y][X] === 1) {
        ctx.font = `${cellSize * 0.6}px Arial`;
        ctx.fillStyle = "black";
        ctx.fillText("Âç†", cx + cellSize * 0.18, cy + cellSize * 0.74);
        mapStatus[Y][X] = 2;

        flagCount++;
        counter.textContent = `Âç† ${minesCount - flagCount}`;
    } else if (mapStatus[Y][X] === 2) {
        if ((X + Y) % 2 === 0) {
            ctx.fillStyle = "white"; // ‰∫ÆËâ≤
        } else {
            ctx.fillStyle = "lightgray"; // ÊöóËâ≤
        }
        ctx.fillRect(cx, cy, cellSize, cellSize);
        mapStatus[Y][X] = 1;

        flagCount--;
        counter.textContent = `Âç† ${minesCount - flagCount}`;
    }
}

// ËÉúÂà©ËÆæÁΩÆ
function checkWin() {
    for (let i = 1; i <= height; i++) {
        for (let j = 1; j < width; j++) {
            if (mapStatus[i][j] === 1 && minesMap[i][j] === 0) {
                return;
            }
        }
    }
    alert("YOU WIN!");
    location.reload();
}

// main
init();
// canvasÁõëÂê¨
canvas.addEventListener("click", (event) => {
    event.preventDefault();
    if (first) {
        generateMines(event.offsetX, event.offsetY);
        first = false;
    }
    uncoverGrid(event.offsetX, event.offsetY);
    checkWin();
});
canvas.addEventListener("contextmenu", (event) => {
    event.preventDefault();
    setFlag(event.offsetX, event.offsetY);
    if (mapStatus[1][2] === 2 && mapStatus[1][3] === 2) {
        alert("12/13:üéÇÊ∞∏Ê≥ΩÁîüÊó•Âø´‰πê~üéÇ");
    }
})
canvas.addEventListener("dblclick", (event) => {
    event.preventDefault();
    uncoverAllGrid(event.offsetX, event.offsetY);
    checkWin();
})

// toolbarÁõëÂê¨
document.getElementById("restart").addEventListener("click", () => {
    init();
})
document.getElementById("mode").addEventListener("click", () => {
    let w = Number(prompt("ËØ∑ËæìÂÖ•Âú∞ÂõæÂÆΩÂ∫¶(9-25)", "9"));
    if (w == null || w < 9 || w > 99) {
        alert("Êó†ÊïàÁöÑÂÆΩÂ∫¶ÔºÅ");
        return;
    }
    let h = Number(prompt("ËØ∑ËæìÂÖ•Âú∞ÂõæÈ´òÂ∫¶(9-25)", "9"));
    if (h == null || h < 9 || h > 99) {
        alert("Êó†ÊïàÁöÑÈ´òÂ∫¶ÔºÅ");
        return;
    }
    let n = Number(prompt("ËØ∑ËæìÂÖ•Âú∞Èõ∑Êï∞Èáè(10-100)", "10"));
    if (n == null || n < 10 || n > 100) {
        alert("Êó†ÊïàÁöÑÊï∞ÈáèÔºÅ");
        return;
    } if (n >= w * h){
        alert("Â∑≤ÁªèË£Ö‰∏ç‰∏ã‰∫ÜÂè£ÁâôÔºÅ");
        return;
    }

    width = w;
    height = h;
    minesCount = n;
    init();
})